<template>
  <div class="cform-container">
    <div class="cform-title">
      <h4>Zainteresowany</h4>
      <h2>Wypenij formularz kontaktowy, a my przygotujemy ofert</h2>
      <p>
        Jeeli zainteresowaa Ci nasza oferta, posiadasz wasny serwis
        internetowy lub chcesz rozpocz swj biznes w internecie, z nam
        zapytanie ofertowe. Oprcz wyceny otrzymasz kilka wskazwek dotyczcych
        moliwych do obrania strategii marketingowych w internecie.
      </p>
    </div>
    <div class="cform-desc">
      <form action="submit">
        <div class="div">
          <input type="text" name="" id="" placeholder="Imi i Nazwisko" />
          <input type="text" name="" id="" placeholder="Adres Email" />
        </div>
        <div class="div">
          <input type="text" name="" id="" placeholder="Nazwa Firmy" />
          <input type="text" name="" id="" placeholder="Telefon" />
        </div>
        <select name="" id="">
          <option value="">Budet</option>
        </select>
        <textarea
          name=""
          id=""
          cols="30"
          rows="10"
          placeholder="Wiadomo"
        ></textarea>
        <input type="button" value="WYLIJ ZAPYTANIE" />
      </form>
    </div>
  </div>
</template>
<script>
export default {
  name: "ContactForm",
  mounted() {
    if (this.$route.path === "/wycena") {
      document.querySelector(".cform-container").classList.add("animate");
    }
    window.addEventListener("scroll", this.animateOnScroll);
  },
  unmounted() {
    window.removeEventListener("scroll", this.animateOnScroll);
  },
  methods: {
    inView() {
      var element = document.querySelector(".cform-container");
      var elementHeight = element.clientHeight;
      var windowHeight = window.innerHeight;
      var scrollY = window.scrollY || window.pageYOffset;

      var scrollPosition = scrollY + windowHeight;
      var elementPosition =
        element.getBoundingClientRect().top + scrollY + elementHeight;

      if (scrollPosition > elementPosition * 0.8) {
        return true;
      }
      return false;
    },
    animateOnScroll() {
      if (this.inView()) {
        document.querySelector(".cform-container").classList.add("animate");
      }
    }
  }
};
</script>
<style scoped lang="scss" src="./ContactForm.style.scss"></style>
